.form-container {
  position: relative;
  text-align: center;
  width: 400px;
  background: white;
  margin: 0px auto;
  padding: 50px 40px;
  box-shadow: 2px 2px 10px 2px gray;
}
.log-in-header {
  font-size: 35px;
  margin-bottom: 50px;
  font-family: "Libre Baskerville", serif;
}
.input-field,
form .button {
  margin: 20px 0;
  position: relative;
  height: 40px;
  width: 100%;
}
.input-field input {
  height: 100%;
  width: 100%;
  border: 1px solid silver;
  padding-left: 15px;
  outline: none;
  font-size: 19px;
  transition: 0.4s;
}
input:focus {
  border: 1px solid #1da1f2;
}
.input-field label,
span.show {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.input-field label {
  left: 15px;
  pointer-events: none;
  color: grey;
  font-size: 18px;
  transition: 0.4s;
}
span.show {
  right: 20px;
  color: #111;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  user-select: none;
  visibility: hidden;
  font-family: "Open Sans", sans-serif;
}
span.show button {
  border: none;
  color: #111;
  font-size: 14px;
  background-color: #00000000;
  font-weight: bold;
  cursor: pointer;
}

input:valid ~ span.show {
  visibility: visible;
}
input:focus ~ label,
input:valid ~ label {
  transform: translateY(-33px);
  background: white;
  font-size: 16px;
  color: #1da1f2;
}
.login-btn {
  width: 100%;
  color: white !important;
}
.login-btn::before {
  clip-path: circle(80% at 50% 50%);
}
.login-btn:hover {
  background-color: #6f2cf3;
}

.form-container .auth {
  margin: 25px 0 20px 0;
  font-size: 19px;
  color: grey;
}
.links {
  display: flex;
  cursor: pointer;
}
.google {
  height: 40px;
  width: 100%;
  border: 1px solid silver;
  background-color: #00000000;
  border-radius: 3px;
  margin: 0 10px;
  transition: all 0.4s ease-out;
  cursor: pointer;
}
.google:hover {
  border: 2px solid #4285f4;
}
.google svg,
.google span {
  color: #4285f4;
}
.links svg {
  font-size: 23px;
  line-height: 40px;
  margin-left: -90px;
}
.links span {
  position: absolute;
  font-size: 17px;
  font-weight: bold;
  padding-left: 8px;
  font-family: "Open Sans", sans-serif;
}
.signup {
  margin-top: 30px;
  font-family: "Libre Baskerville", serif;
}
.signup a {
  color: #3498db;
  text-decoration: none;
}
.signup a:hover {
  text-decoration: underline;
}
